<template>
  <div>
    <button @click="logout">Salir del Programa</button>
  </div>
</template>

<script>
import axios from '../axios';

export default {
  methods: {
    goToMenu() {
      // Obtenemos el tipo de usuario almacenado en localStorage
      const tipoUsuario = localStorage.getItem('tipo_usuario');
      
      // Redirigir a la pgina correcta segn el tipo de usuario
      if (tipoUsuario === 'administrador') {
        this.$router.push('/menu');  // Redirigir al men principal del administrador
      } else if (tipoUsuario === 'supervisor') {
        this.$router.push('/menu-supervisor');  // Redirigir al men del supervisor
      } else if (tipoUsuario === 'vendedor') {
        this.$router.push('/menu-vendedor');  // Redirigir al men del vendedor
      }
    },
    logout() {
      const tipoUsuario = localStorage.getItem('tipo_usuario');

      if (tipoUsuario === 'vendedor') {
        const vendedorId = localStorage.getItem('vendedor_actual');
        
        axios.post('/update-vendedor-estado', {
          vendedor_id: vendedorId,
          estado: false
        })
        .then(() => {
          console.log("Vendedor marcado como fuera de lnea al salir.");
        })
        .catch(error => {
          console.error("Error al marcar al vendedor como fuera de lnea:", error);
        });
      }

      localStorage.removeItem('tipo_usuario');
      this.$router.push('/');
    }
  }
};
</script>

<style scoped>
button {
  margin: 10px;
  padding: 10px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: #2980b9;
}
</style>
