<template>
    <div>
      <!-- Botn Regresar que redirige al SupervisorLeadsComponent 
      <button @click="regresar" class="btn btn-primary mb-3">Regresar</button> -->
      <h2>Estado de Conexin de Vendedores</h2>
      <button @click="obtenerEstadoVendedores">Verificar Conexin Vendedores</button>
  
      <table v-if="vendedores.length > 0" class="estado-vendedores">
        <thead>
          <tr>
            <th>Nombre del Vendedor</th>
            <th>Estado de Conexin</th>
            <th><button @click="refrescarEstadoVendedores">Refrescar</button></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="vendedor in vendedores" :key="vendedor.id">
            <td>{{ vendedor.nombre }}</td>
            <td>
              <span :class="vendedor.en_linea ? 'online' : 'offline'">
                {{ vendedor.en_linea ? 'En lnea' : 'Fuera de lnea' }}
              </span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  import axios from '../axios';
  
  export default {
    data() {
      return {
        vendedores: []
      };
    },
    methods: {
      obtenerEstadoVendedores() {
        axios.get('/estado-vendedores')
          .then(response => {
            this.vendedores = response.data.vendedores;
          })
          .catch(error => {
            console.error("Error al obtener el estado de los vendedores:", error);
          });
      },
      refrescarEstadoVendedores() {
        this.obtenerEstadoVendedores();
      },
      regresar() {
        this.$router.push('/leads-supervisor');  // Redirigir a la pgina de supervisin de leads
      }
    }
  };
  </script>
  
  <style>
  .estado-vendedores {
    width: 100%;
    border-collapse: collapse;
  }
  .estado-vendedores th, .estado-vendedores td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  .estado-vendedores th {
    background-color: #f2f2f2;
  }
  .online {
    color: green;
    font-weight: bold;
  }
  .offline {
    color: red;
    font-weight: bold;
  }
  </style>
  